<template>
		<v-card>
			<v-card-title>
		<v-input>
			<v-spacer></v-spacer>
			<v-text-field
					v-model="tableFilter"
					append-icon="search"
					label="Search"
					single-line
			></v-text-field>
		</v-input>
			</v-card-title>
		<vue-ads-table-tree
				:columns="tableColumns"
				:rows="tableRows"
				:filter="tableFilter"
				:page="tablePage"
				:itemsPerPage="itemsPerPage"
				@filter-change="filterChanged"
				@page-change="pageChanged"
		>
			<template slot="firstName" slot-scope="props">
				{{props.row.firstName}}
			</template>
			<template slot="read" slot-scope="props">
				<v-checkbox
						v-if="typeof props.row.read != 'undefined'"
						v-model="props.row.read"
						:readonly="props.row.readReadonly"
						@change="changeRead(props.row)"
						hide-details
				/>
				<p v-else class="no-margin-bottom">-</p>
			</template>
			<template slot="write" slot-scope="props">
				<v-checkbox
						v-if="typeof props.row.write != 'undefined'"
						v-model="props.row.write"
						:readonly="props.row.writeReadonly"
						@change="changeWrite(props.row)"
						hide-details
				/>
				<p v-else class="no-margin-bottom">-</p>
			</template>
			<template slot="delete" slot-scope="props" style="text-align: center;">
				<v-checkbox
						v-if="typeof props.row.delete != 'undefined'"
						v-model="props.row.delete"
						:readonly="props.row.deleteReadonly"
						@change="changeDelete(props.row)"
						hide-details
				/>
				<p v-else class="no-margin-bottom">-</p>
			</template>
		</vue-ads-table-tree>
		</v-card>
</template>


<style>
	.v-input--selection-controls {
		margin-top: 0 !important;
		padding-top: 0 !important;
	}

	.v-input__slot {
		margin-bottom: 0 !important;
	}

	td.vue-ads-py-2 {
		padding-top: 10px !important;
		padding-bottom: 10px !important;
		margin-top: 0 !important;
		margin-bottom: 0 !important;
	}

	th.vue-ads-py-2 {
		padding-top: 19px !important;
		padding-bottom: 19px !important;
		margin-top: 0 !important;
		margin-bottom: 0 !important;
		background-color: #fff;
	}

	.vue-ads-border-r {
		border-right-width: 0px;
	}

	.vue-ads-bg-grey-lightest {
		background-color: #fff;
	}

	p.no-margin-bottom {
		margin-bottom: 0px;
	}

	.vue-ads-py-3, .vue-ads-m-2 {
		display: none;
	}
	.vue-ads-border {
		border: none;
	}
	.vue-ads-border-b {
		border-top-width: 1px;
	}
</style>


<script>
	import VueAdsTableTree from 'vue-ads-table-tree'
	//import VueAdsPagination from 'vue-ads-pagination'
	//	import VueAdsPageButton from 'vue-ads-pagebutton'

	import '../../node_modules/@fortawesome/fontawesome-free/css/all.css';
	import '../../node_modules/vue-ads-table-tree/dist/vue-ads-table-tree.css';

	export default {
		components: {
			VueAdsTableTree,
//			VueAdsPagination,
//			VueAdsPageButton,
		},
		props: [
			'columns',
			'rows',
			'classes',
			'filter',
			'start',
			'end',
			'slots',
			'export-name',
			'full-export',
			'call-rows',
			'call-children',
			'call-temp-rows',
			'page',

		],
		data() {
			return {
				tableColumns: this.columns,
				tableRows: this.rows,
				tableClasses: this.classes,
				tableFilter: this.filter,
				tableStart: this.strat,
				tablePage: this.page,
				itemsPerPage: 10000,
			}

		},
		methods: {
			filterChanged(filter) {
				this.tableFilter = filter;
			},

			pageChanged(page) {
				this.tablePage = page;
			},
			changeRead(item) {
				this.$emit('change', item);
				var thisHolder = this;
				if (typeof item._children != "undefined") {
					item._children.forEach(function (el) {
						if (typeof el.read != "undefined") {
							el.read = el.readReadonly = item.read;
						}
						thisHolder.changeRead(el);
					});
				}
			},
			changeWrite(item) {
				this.$emit('change', item);
				var thisHolder = this;
				if (typeof item._children != "undefined") {
					item._children.forEach(function (el) {
						if (typeof el.write != "undefined") {
							el.write = el.writeReadonly = item.write;
						}
						thisHolder.changeWrite(el);
					});
				}
			},
			changeDelete(item) {
				this.$emit('change', item);
				var thisHolder = this;
				if (typeof item._children != "undefined") {
					item._children.forEach(function (el) {
						if (typeof el.delete != "undefined") {
							el.delete = el.deleteReadonly = item.delete;
						}
						thisHolder.changeDelete(el);
					});
				}
			},
			initAccessSate(item) {
				var thisHolder = this;
				if (typeof item._children != "undefined") {
					item._children.forEach(function (el) {
						if (typeof el.read != "undefined") {
							el.readReadonly = item.read;
						}
						if (typeof el.write != "undefined") {
							el.writeReadonly = item.write;
						}
						if (typeof el.delete != "undefined") {
							el.deleteReadonly = item.delete;
						}
						thisHolder.initAccessSate(el);
					});
				}
			}
		},
		watch: {},
		mounted() {
			var thisHolder = this;
			this.tableRows.forEach(function (el) {
				thisHolder.initAccessSate(el);
			});
		},
	}

</script>
